.searchContainer {
  display: flex;
  justify-content: center;
  margin: 10px;
}

.searchInput {
  padding: 8px 15px;
  font-size: 30px;
  width: 500px;
  border-width: 0;
  background: #a592f2;
  color: #e7e9f0;
  border-radius: 50px;
  box-shadow: 0 0 5px rgba(255, 255, 255, 0.5);
}

.searchInput::placeholder {
  color: #e7e9f0;
}

.searchInput:focus {
  outline: none;
}

@media (max-width: 767px) {
  .searchInput {
    padding: 8px 15px;
    font-size: 24px;
    width: 320px;
    border-width: 0;
    background: #a592f2;
    color: #e7e9f0;
    border-radius: 50px;
    box-shadow: 0 0 5px rgba(255, 255, 255, 0.5);
  }
}
